import './Popup.scss';
import { Home, InfoOutlined} from '@material-ui/icons';
import { useState } from 'react';

const Popup = ({closePopup}) => {

    const handleClose = (e) => {
        e.preventDefault();
        closePopup(false);
    }

    return (
        <div className="popup">
            <div className="popup-container">
                <div className="popup-left">
                    <Home className="popup-icons"/>
                    <span className="popup-text">EV</span>
                </div>
                <div className="popup-right">
                    <span className="address">
                        Yedi Eyll Mah. 1200. Sokak No:4 Efeler/Aydn
                    </span>
                </div>
            </div>
            <div className="popup-info">
                <div className="info-container">
                    <InfoOutlined className='info-icon'/>
                    <span className="info-icon-text">HATIRLATMA</span>
                </div>
                <span className="info-text">
                    Mobil ube her ay ayn <b>15. gn</b> evinize otomatik gelecektir.
                    Eve mobil ube servisini <b>bu ay 3</b> kere daha arabilirsiniz.
                </span>
            </div>
            
            <button className="call-service button">
                MD AIR
            </button>
            <button className="call-help button">
                YARDIM AIR
            </button>
            <button onClick={handleClose} className='close-button button'>
                IKI YAP
            </button>
        </div>
    )
}

export default Popup