import './AlternativeLogin.scss';
import TopBar from '../../components/TopBar/TopBar';
import RegisterBottom from '../../components/RegisterBottom/RegisterBottom';
import { DescriptionOutlined, DirectionsCarOutlined, ImportExportOutlined,
LocalAtmOutlined,
MicOutlined} 
from '@material-ui/icons';
import useSpeechToText from 'react-hook-speech-to-text';
import Popup from '../../components/Popup/Popup';
import { useEffect, useState } from 'react';
import Tooltip from '@mui/material/Tooltip';
import { useNavigate } from 'react-router-dom';
import { useSpeechSynthesis } from "react-speech-kit";

const AlternativeLogin = () => {
    const navigate = useNavigate();
    const { speak } = useSpeechSynthesis();
    const [popupVisibility, setPopupVisiblity] = useState(false);
    const handlePopupOpen = (e) => {
        e.preventDefault();
        setPopupVisiblity(true);
    }

    const {
        error,
        interimResult,
        isRecording,
        results,
        startSpeechToText,
        stopSpeechToText,
    } = useSpeechToText({
        continuous: true,
        // crossBrowser: true,
        // googleApiKey: 'API_KEY',
        // googleCloudRecognitionConfig: {
        //     languageCode: 'tr-TR'
        // },
        useLegacyResults: false,
        speechRecognitionProperties: {
            interimResults: true
        },
        timeout: 500
    });

    const handleMic = (e) => {
        if (isRecording){
            speak({ text: 'Mteri hizmetleri aranyor', lang: "tr-TR"})
            stopSpeechToText();
            e.target.classList.remove('active');
        } else{
            startSpeechToText();
            e.target.classList.add('active');
        }
    }

    useEffect(() => {
        for (let i = 0; i < results.length; i++) {
            let res = results[results.length-1].transcript;
            console.log(res);
            if (res.includes("banka") && popupVisibility === false){
                setPopupVisiblity(true);
            } else if (res.includes("giri")){
                navigate("/");
                console.log("Giri yaplyor...");
            }
            break;
        }
    }, [results])

    return (
        <div className='login'>
            <TopBar isLogin={true}/>
            <div className="background"/>
            <div className='background-shape'/>

            <div className='center'>
                <span className="welcomeText">Ho geldiniz</span>
                <span className="nameText">Recep ifti</span>
                <span className="change-account-text">Hesap deitir</span>
                <button className="login-button"
                onClick={() => navigate("/")}>
                    Giri Yap
                </button>
                <div className='forget-password-container'>
                    <div/>
                    <span className="forget-passoword-text">ifremi Unuttum</span>
                </div>
            </div>

            <div className="mic-container">
                <div className="mic-background">
                    <MicOutlined className="mic-icon" onClick={handleMic}/>
                </div>
            </div>

            <div className='action-bottom'>
                <div className="action-container" onClick={handlePopupOpen}>
                    <DirectionsCarOutlined className='action-icon'/>
                    <span className="action-text">Mobil Ara</span>
                </div>
                <div className="action-container">
                    <DescriptionOutlined className='action-icon'/>
                    <span className="action-text">Fatura de</span>
                </div>
                <div className="action-container">
                    <ImportExportOutlined className='action-icon'/>
                    <span className="action-text">Para Gnder/Al</span>
                </div>
                <div className="action-container">
                    <LocalAtmOutlined className='action-icon'/>
                    <span className="action-text">Para Yatr/ek</span>
                </div>
            </div>

            {popupVisibility ? <Popup closePopup={setPopupVisiblity}/> : null}

            <RegisterBottom isLogin={true}/>
        </div>
    )
}

export default AlternativeLogin